/*CSS RESET*/

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role='list'],
ol[role='list'] {
  list-style: none;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/*END RESET*/

/*FONTS*/
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;900&display=swap');
/*END FONTS*/

/*VARIABLES*/
:root {
    --dark-purple: rgb(48, 29, 87);
    --light-purple: #6f3180;
    --white: #ffffff;
    --green: #24805d;
    --purple-grad-bg: linear-gradient(-35deg, rgba(48, 29, 87,0.9) 30%, var(--light-purple)), url(../img/carbon-fibre-texture.png);
    --blue: rgb(23, 104, 178);
    --dark-blue: rgb(36, 55, 131);

    --phone-offset: -130px;
  }
/*END VARIABLES*/

/* FIX FOR GAP CREATED BY MOVING PHONE UP*/
.move-up {
    top: -160px;
    position: relative;
}

@media only screen and (max-width: 1000px) {
    .move-up {
        top: 0;
    }
}

body {
    background-color: var(--blue);
}

/*navbar*/

nav {
    background-color: var(--dark-purple);
}
.nav-wrapper a {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo {
    height: 90%;
    width: auto;
}

/*end navbar*/

/*hero*/

.hero {
    height: 500px;
    background-image: url(../img/greeny-music-hero-2.jpg);
    background-size: cover;
    background-position: 15%;
    background-color: var(--dark-purple);
}

.hero-logo {
    display: none;
    height: 250px;
    width: auto;
}

@media only screen and (min-width: 769px) {
    .hero {
        height: 570px;
        background-position: left center;
    }
    .hero-logo {
        display: unset;
        position: relative;
        left:50%;
        top: 50%;
        transform: translate(-130%, -60%);
        filter: drop-shadow(1px 1px 8px rgba(0,0,0,0.8));
    }
}

@media only screen and (min-width: 1024px) {
    .hero {
        background: url(../img/greeny-music-5.jpg);
        height: 950px;
        background-position: 30% center;
    }
}

/*end hero*/

/*Single section*/

.single-details {
    position: relative;
    display: flex;
    flex-direction: column;
    background: var(--purple-grad-bg);
    border-top: 15px solid var(--light-purple);
    border-bottom: 15px solid var(--light-purple);
    z-index: 0;
}

.album-cover {
    display: none;
}

.mobile-mockup {
    position: relative;
    width: fit-content;
    margin: 0 auto;
    top: var(--phone-offset);
}

.second-mob {
    top: 0;
}

.streaming-container {
    position: relative;
    top: calc(var(--phone-offset) + 50px);
    margin: 60px auto;
    max-width: 500px;
}

.second-single .streaming-container {
    top: 70px;
}

.single-wrapper h1 {
    color: var(--white);
    font-weight: 900;
    text-align: center;
    text-transform: uppercase;
    
    font-size: 3rem;
}

.strm-head:nth-of-type(2) {
    font-size: 1.5rem;
    font-weight: 500;
}

.arrow-1 {
    width: 40px;
    height: auto;
    position: absolute;
    top: 125px;
    right: clamp(2rem, 15vw, 5rem);
    transform-origin: 0% 100%;
    transform: translate(30px, -20px);
}

.streaming-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin: 60px 0;
}

.streaming-btn {
    width: 90%;
    height:60px;
    margin-bottom: 1rem;
    background-color: rgba(255,255,255,0.2);
    border: none;
    color: white;
    padding: 0 0 0 24px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    display: grid;
    grid-template-columns: auto 60px;
    justify-content: space-between;
    align-items: center;
    border-right: 5px solid var(--light-purple);
}

.streaming-btn span:nth-of-type(2) {

    background-color: var(--dark-purple);
    height: 60px;
    width: 60px;
    margin: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.streaming-btn span:nth-of-type(2) i {
    font-size: 32px;
}

.streaming-btn:hover {
    border-right: 5px solid var(--green);
    background-color: rgba(255,255,255,0.25);
}

.streaming-btn:hover i {
    color: var(--green);
}

.streaming-btn:active {
    border-right: 5px solid var(--green);
    background-color: rgba(255,255,255,0.5);
}

@media only screen and (min-width: 1025px) {
    .single-wrapper {
        width: 1000px;
        display: flex;
        margin: 0 auto;
        justify-content: center;
        gap: 50px;
    }
    .mobile-mockup {
        width: 420px;
        justify-self: start;
        margin: 0;
        top: -260px;
    }
    .streaming-container {
        width: 420px;
        top: 0;
        margin-top: 5rem;
    }
    .arrow-1 {
        width: unset;
        top: 35px;
        right: 0;
    }
    .album-cover {
        display: unset;
        width: 480px;
        height: 480px;
        position: absolute;
        transform-origin: 0% 100%;
        filter: drop-shadow(3px 3px 3px rgba(0,0,0,0.4));
    }

    .missing-cover {
        overflow: hidden;
        transform: translate(-432px, 276px) rotate(-5deg);
    }

    .sirens-cover {
        overflow: hidden;
        transform: translate(386px, 360px) rotate(2.9998deg) scale(1.00001, 1.00001);
    }

    /*.album-cover:nth-of-type(1) {
        
    }
    .album-cover:nth-of-type(2) {
        transform: translate(-361px, 453px) rotate(-10deg);
    }
    .album-cover:nth-of-type(3) {
        transform: translate(-361px, 453px) rotate(-14deg);
    }*/
}

/* end single section*/

/*EP SECTION*/

.arrow-2 {
    width: 60px;
    height: auto;
    margin: 0 auto;
    position: relative;
    top: -75px;
    left: -221px;
    z-index: 3;
    transform-origin: 100% 100%;
    visibility: hidden;
}

.ep-details {
    
    background: radial-gradient(circle at 60% 80%,rgba(23,104,178,.95)10%,black 80%);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 10px 100px 10px;
    z-index: 2;
}

.ep-details h2 {
    text-transform: uppercase;
    text-align: center;
    font-weight: 900;
    font-size: 2rem;
    color: var(--white);
}
.ep-details h3 {
    display: inline-block;
    text-transform: none;
    font-weight: 500;
    font-size: 1.5rem;
    color: var(--white);
}

.ep-container {
    position: relative;
    width: 400px;
    height: 300px;
    margin: 50px auto;
    display: none;
}

.ep-container-lrg {
    margin: 50px auto;
    
}

.ep-container img {
    position: absolute;
    top: 0;
    width: 300px;
}
.ep-container img:first-of-type {
    left: 0;
    z-index: 2;
    box-shadow: 0 0 8px rgba(0,0,0,0.8);
}
.ep-container img:last-of-type {
    right: 0;
    z-index: 1;
}

.arrow-3 {
    display: none;
    transform: scale(0.5);
    position: relative;
}



@media only screen and (min-width: 1000px) {
    .ep-container-lrg {
        display: none;
    }
    .ep-container {
        position: relative;
        width: 700px;
        height: 500px;
        margin: 50px auto;
        display: flex;
        justify-content: center;
    }
    .ep-container img {
        position: absolute;
        top: 0;
        width: 500px;
    }
    .ep-container img:first-of-type {
        left: unset;
    }
    .ep-container img:last-of-type {
        right: unset;
    }
    .arrow-3 {
        display: block;
        bottom: 0;
        transform: translate(200%, 22%) scale(1);
        z-index: 2;
    }
    .arrow-2 {
        width: unset;
        top: -266px;
        transform: translate(44.77px, 130px) scale(0.8);
        z-index: 2;
        visibility: visible;
    }
}

/*GALLERY SECTION*/

.gallery {
    
    max-width: 1000px;
    margin: 50px auto;
    z-index: 1;
}

/*GALLERY SECTION*/

/*BIO SECTION*/


.bio {
    padding: 50px 20px;
    background-color: var(--white);
    text-align: center;
}

.bio a {
    color: #ffffff;
    font-weight: 900;
}

.bio h3 {
    text-transform: uppercase;
    font-weight: 900;
    font-size: 2rem;
    margin-bottom: 20px;
}

.bio p {
    margin-bottom: 20px;
    font-weight: 400;
    line-height: 150%;
}

.bio-logo {
    width: auto;
    height: 50px;
    margin: 0 auto;
    
}

.arrow-4 {
    display: none;
    z-index: 2;
}

.bio-img {
    width: auto;
    margin: 0 auto;
}

@media only screen and (min-width: 1000px) {
    .bio-container {
        position: relative;
        height: auto;
        background: var(--purple-grad-bg);
    }
    .arrow-4 {
        display: unset;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate(-300px, -50%);
    }
    .bio-wrapper {
        position: relative;
        width: 1000px;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 50px;
        justify-content: center;
        align-items: center;
    }
    .bio {
        position: relative;
        background-color: var(--light-purple);
        padding: 50px;
        color: var(--white);
        
        z-index: 3;
    }  
    .bio h3 {
        color: #fff;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(68%, 90%);
        font-size: 15rem;
        z-index: -1;
        filter: opacity(0.7);
        
    }
    .decor {
        position: absolute;
        height: 50px;
        width: 90%;
        background: linear-gradient(90deg, var(--dark-blue), rgba(23, 104, 178,0) ) ;
        z-index: 2;
        bottom: 125px;
        right: 0;
    }
    .bio-img {
        position: relative;
        top: -50px;
        
        z-index: 1;
    }
}

/*SOCIAL STRAP*/
footer {
    color: var(--white);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 40px 10px;
}

footer a, footer a:hover, footer a:active {
    color: #ffffff;
    text-decoration: underline;
}

footer h3 {
    font-size: 1.7rem;
    font-weight: 500;
    text-transform: uppercase;
    text-align: center;
}
.social-icons {
    font-size: 4rem;
}

.social-icons a {
    color: var(--white);
}

.social-icons a:hover i {
    transform-origin: 50% 100%;
    transform: scale(1.05);
}